<template>
  <div class="productDetails">
    <div class="productDetailsHeader">
      <div class="headerContent">
        <van-icon name="arrow-left" @click="$router.go(-1)" />
        <span>{{ $t("index.text23") }}</span>
      </div>
    </div>
    <div class="slot"></div>

    <div class="productDetailsMain">
      <img
        :src="productInfo.full_url"
        alt=""
        class="productDetailsMainHeader"
      />
      <div class="productDetailsMainContent">
        <div class="priceTitle">
          <div class="price">{{ productInfo.price }}HF</div>
          <div>
            <span style="color: #666666">{{ $t("index.text24") }}</span>
            <span>{{ $t("index.text25") }}</span>
          </div>
        </div>

        <div class="postage mt2r jcsb">
          <span style="color: #666666">{{ $t("index.text26") }}</span>
          <span style="color: #d12c21; font-weight: bold"
            >{{ productInfo.shipping }}HF</span
          >
        </div>

        <div class="productName mt2r">
          {{ productInfo.title }}
        </div>

        <div class="line"></div>

        <div class="merchantInfo">
          <div class="merchantName">{{ $t("index.text27") }}</div>
          <div class="creditScore jcsb">
            <div style="display: flex; align-items: center">
              <img src="../../assets/img/pic3.png" alt="" />
              <span>{{
                productInfo.shopInfo && productInfo.shopInfo.shop_name
              }}</span>
            </div>
            <div>
              {{ productInfo.shopInfo && productInfo.shopInfo.credits
              }}{{ $t("index.text28") }}
            </div>
          </div>
        </div>

        <div class="productInfo mt2r">
          <div class="productInfoTitle">{{ $t("index.text29") }}</div>
          <div class="info mt2r">
            {{ productInfo.description }}
          </div>
        </div>

        <div class="buyBtn jcsb mt2r">
          <div class="buyBtnLeft jcc">{{ $t("index.text30") }}</div>
          <div class="buyBtnRight jcc" @click="showPaymentPopup = true">
            {{ $t("index.text31") }}
          </div>
        </div>
      </div>
    </div>

    <van-popup
      v-model="showPaymentPopup"
      position="bottom"
      class="showPaymentPopup"
    >
      <div class="receivingAddress">
        <div
          class="receivingAddressTitle"
          @click="$router.push('/addressManagement')"
        >
          <div class="receivingAddressText">
            <img src="../../assets/img/pic4.png" alt="" />{{
              $t("index.text32")
            }}
          </div>

          <van-icon name="arrow" />
        </div>

        <div v-if="addressList.length > 0">
          <div class="userInfo mt2r jcsb">
            <span>{{ defaultAddress.name }}</span>
            <span>{{ defaultAddress.phone }}</span>
          </div>

          <div class="userAddress mt2r">
            {{ defaultAddress.address }}
          </div>
        </div>

        <div class="emptyAddress" v-else>
          <img src="../../assets/img/pic19.png" alt="" />

          <div style="margin-top: 1rem">{{ $t("index.text33") }}</div>
        </div>
      </div>

      <div class="productInfoBox mt2r">
        <img :src="productInfo.full_url" alt="" class="productInfoBoxLeft" />
        <div class="productInfoBoxRight">
          <div class="name">
            {{ productInfo.title }}
          </div>
          <div class="price">{{ productInfo.price }}HF</div>
        </div>
      </div>

      <div class="selectQuantity mt2r jcsb">
        <div class="text">{{ $t("index.text34") }}</div>
        <van-stepper
          v-model="selectQuantity"
          theme="round"
          button-size="22"
          disable-input
        />
      </div>

      <div class="postage mt2r jcsb">
        <span>{{ $t("index.text35") }}</span>
        <span style="font-weight: bold; color: #d12c21"
          >{{ productInfo.shipping }}HF</span
        >
      </div>

      <div class="line"></div>

      <div class="postage mt2r jcsb">
        <span>{{ $t("index.text36") }}</span>
        <span style="font-weight: bold; color: #d12c21; font-size: 2rem"
          >{{ summation.toFixed(1) }}HF</span
        >
      </div>

      <div class="paymentBtn mt2r jcc" @click="payment">
        {{ $t("index.text37") }}
      </div>
    </van-popup>
  </div>
</template>

<script>
import BigNumber from "bignumber.js";

export default {
  components: {},
  data() {
    return {
      showPaymentPopup: false,
      btnState: true,
      selectQuantity: 1,
      productInfo: {},
      addressList: [],
      defaultAddress: {}
    };
  },
  computed: {
    summation() {
      // 处理浮点数运算进度问题
      const price = new BigNumber(this.productInfo.price);
      const quantity = new BigNumber(this.selectQuantity);
      const shipping = new BigNumber(this.productInfo.shipping);
      const totalPrice = price.times(quantity).plus(shipping);
      return totalPrice;
    }
  },
  async activated() {
    this.productInfo = JSON.parse(this.$route.query.productInfo);
    this.getShippingAddress();
  },

  methods: {
    getShippingAddress() {
      this.$api
        .get("address/index", {
          page: 1,
          limit: 99999
        })
        .then(res => {
          this.addressList = res.data.list;
          let defaultAddress = res.data.list.filter(
            item => item.is_default == 1
          )[0];

          if (defaultAddress) {
            this.defaultAddress = defaultAddress;
          } else {
            this.defaultAddress = res.data.list[0];
          }
        });
    },

    // 生成支付订单
    payment() {
      if (!this.btnState) return;
      this.btnState = false;
      this.$api
        .post("orders/add", {
          title: this.productInfo.title,
          price: this.productInfo.price,
          shipping: this.productInfo.shipping,
          image: this.productInfo.image,
          shop_id: this.productInfo.shopInfo.id,
          quantity: this.selectQuantity,
          product_id: this.productInfo.id,
          total:
            Number(this.productInfo.price) * Number(this.selectQuantity) +
            Number(this.productInfo.shipping),
          address_id: this.defaultAddress.id
        })
        .then(res => {
          if (res.code != 200) {
            this.$toast(res.msg);
            this.btnState = true;
          } else {
            this.$toast(this.$t("index.text38"));
            this.showPaymentPopup = false;
            setImmediate(() => {
              this.btnState = true;
            }, 1000);
          }
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.productDetails {
  height: 100%;
  overflow: scroll;
}
.productDetailsHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}
.productDetailsMainHeader {
  width: 100%;
  height: 37.5rem;
}

.productDetailsMainContent {
  padding: 2rem 1.5rem;
  position: relative;
  margin-top: -1.5rem;
  border-radius: 1.5rem 1.5rem 0 0;
  background: #fffbf1;
  .priceTitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .price {
      font-size: 2.6rem;
      font-weight: bold;
      color: #d12c21;
    }
  }

  .productName {
    font-size: 1.6rem;
    font-weight: bold;
    line-height: 2.8rem;
  }

  .line {
    height: 0.1rem;
    background: #f5c96d;
    margin: 2rem 0;
  }

  .merchantInfo {
    height: 8.2rem;
    padding: 1.4rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: url("../../assets/img/pic26.png") no-repeat;
    background-size: 100% 100%;

    .merchantName {
      font-weight: bold;
      padding: 0 1.4rem;
    }

    .creditScore {
      padding: 0 1.4rem;
      img {
        width: 1.6rem;
        height: 1.6rem;
        margin-right: 0.5rem;
      }
    }
  }

  .productInfo {
    padding: 1.4rem;
    background: rgba(245, 201, 109, 0.3);
    border-radius: 0.4rem;
    .productInfoTitle {
      font-weight: bold;
    }
    .info {
      line-height: 2.8rem;
    }
  }

  .buyBtn {
    .buyBtnLeft {
      width: 13.8rem;
      height: 4rem;
      border-radius: 0.4rem;
      border: 0.2rem solid #f5c96d;
      color: #f5c96d;
      font-size: 1.6rem;
      font-weight: bold;
    }

    .buyBtnRight {
      width: 18.4rem;
      height: 4rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
      border-radius: 0.4rem;
      font-size: 1.6rem;
      font-weight: bold;
      color: #fff;
    }
  }
}

.showPaymentPopup {
  border-radius: 2rem 2rem 0 0;
  padding: 1.6rem;

  .receivingAddress {
    background: rgba(245, 201, 109, 0.3);
    border-radius: 0.8rem;
    padding: 1.5rem;

    .receivingAddressTitle {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .receivingAddressText {
        display: flex;
        align-items: center;
        font-weight: bold;
        img {
          width: 1.6rem;
          height: 1.6rem;
          margin-right: 0.5rem;
        }
      }

      .van-icon {
        font-size: 1.8rem;
        color: #d5d2cd;
      }
    }

    .userInfo {
      span {
        font-weight: bold;
      }

      .userAddress {
        line-height: 2.5rem;
      }
    }

    .emptyAddress {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      img {
        width: 8.65rem;
        height: 6.4rem;
      }
      div {
        font-weight: bold;
      }
    }
  }

  .productInfoBox {
    display: flex;
    .productInfoBoxLeft {
      width: 10rem;
      height: 10rem;
    }

    .productInfoBoxRight {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-left: 0.8rem;

      .name {
        font-size: 1.2rem;
        font-weight: bold;
        line-height: 2.8rem;
      }

      .price {
        font-size: 1.6rem;
        color: #d12c21;
        font-weight: bold;
      }
    }
  }

  .selectQuantity {
    .van-stepper {
      background: #ffffff;
      border-radius: 4.5rem;
      border: 1px solid #cccccc;
      padding: 0.2rem;
    }
  }

  .line {
    margin: 2rem 0;
    background: #cccccc;
    height: 0.1rem;
  }
  .paymentBtn {
    height: 5rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    color: #fff;
    font-size: 1.6rem;
  }
}
</style>



